.App {
  text-align: center;
}

.App-header {
  background: linear-gradient(to right,rgb(79,138,87), rgb(179,41,38));
  min-height: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-main {
  padding: 20px;
}

.scores-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.score-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f0f0f0;
  margin: 10px 0;
  padding: 10px;
  border-radius: 8px;
  width: 80%;
  max-width: 600px;
}

.time {
  font-size: 1em;
  font-weight: bold;
  margin-bottom: 5px;
}

.teams {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.team-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 40%;
  min-width: 20%;
}

.team-logo {
  width: 40px;
  height: 40px;
  margin-bottom: 5px;
}

.team {
  font-size: 1.3em;
  font-weight: bold;
}

.score-separator {
  display: flex;
  align-items: center;
}

.score {
  font-size: 1.5em;
  font-weight: bold;
  color: #ff0000;
  margin: 0 5px;
}

.colon {
  font-size: 1.5em;
  font-weight: bold;
  margin: 0 5px;
}

.App-footer {
  background-color: #282c34;
  padding: 10px;
  color: white;
  width: 100%;
}


.App-nav {
  background-color: #f0f0f0;
  padding: 10px 0;
}

.App-nav ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.App-nav li {
  margin: 0 15px;
}

.App-nav a {
  text-decoration: none;
  color: #000;
  font-size: 1.2em;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.App-nav a:hover {
  background-color: #ddd;
}

.App-nav a.active {
  background-color: #282c34;
  color: white;
}

.points {
  font-size: 1.5em;
  font-weight: bold;
  color: #000;
  margin-left: 10px;
}