.scores-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .score-table {
    width: 80%;
    max-width: 600px;
    border-collapse: collapse;
  }
  
  .score-row {
    background-color: #f0f0f0;
    margin: 10px 0;
    padding: 10px;
    border-radius: 8px;
  }
  
  td.team {
    font-size: 1.5em;
    font-weight: bold;
    text-align: left;
    padding: 10px;
    border: 1px solid #ddd;
  }
  
  td.points {
    font-size: 1.5em;
    font-weight: bold;
    color: #000;
    text-align: right;
    padding: 10px;
    border: 1px solid #ddd;
  }